import videoobject from "@/components/script/videoobject";
import videoitemobject from "@/components/script/videoitemobject";

export default {
  name: "video_side_item",
  mixins: [videoobject, videoitemobject],
  props: {
    topic_data: {},
  }
  ,
  data: function () {
    return {
      preivew_url: undefined,
      online_video: undefined
    }
  }
  ,
  created() {
    this.online_video = this.topic_data.video_info.online_videos[0];
  }
  ,
  computed: {}
  ,
  methods: {
    limit_video_title: function () {
      let title = this.video_title();
      if (title.length > 25) {
        return title.slice(0, 25) + "...";
      }
      return title
    }
  }
}
