import videoobject from "@/components/script/videoobject";
import videoitemobject from "@/components/script/videoitemobject";
import video_book_item from "@/components/video/item/video_book_item";

export default {
  name: "video_book_list",
  mixins: [videoobject, videoitemobject],
  components: {
    video_book_item
  },
  props: {
    topic_datas: undefined
  },
  data() {
    return {
      video_book_list_width: undefined,
      class_name: 'col-3'
    }
  },
  created() {
    this.update_width();

    window.addEventListener('resize', this.on_resize);
  },
  methods: {
    on_resize: function () {
      this.video_book_list_width = undefined;
      this.update_width();
    },
    update_width: function () {
      if (!this.$refs['book_list']) {
        return setTimeout(this.update_width, 111);
      }
      this.video_book_list_width = this.$refs['book_list'].clientWidth;
      console.log('video_book_list_width:', this.video_book_list_width);
      this.update_list();
    },
    update_list: function () {
      var ROW_COUNT = parseInt(this.video_book_list_width / 233);
      ROW_COUNT = ROW_COUNT < 2 ? 2 : ROW_COUNT;
      ROW_COUNT = ROW_COUNT > 4 ? 4 : ROW_COUNT;
      this.class_name = 'col-' + parseInt(12 / ROW_COUNT);
      console.log('class_name:',this.class_name);
    }
  }
}
