import videoobject from "@/components/script/videoobject";
import videoitemobject from "@/components/script/videoitemobject";

export default {
  name: "video_book_item",
  mixins: [videoobject, videoitemobject],
  props: {
    topic_data: {},
    width: undefined,
    height: undefined,
  },
  data() {
    return {
      video_item_width: undefined,
      video_item_height: undefined
    }
  },
  mounted() {
    this.update_width();
  },
  methods: {
    update_width: function () {
      if (!this.$refs['book_item']) {
        return setTimeout(this.update_width, 50);
      }
      this.video_item_width = this.$refs['book_item'].clientWidth;
      console.log('video_item_width:', this.video_item_width);
      this.video_item_height = this.video_item_width * 1.32;
      this.video_item_height = parseInt(this.video_item_height);
      console.log('video_item_width:', this.video_item_width);
    }
  }
}
